  /* rem-reset.js | By:from the network  | 2017.4.15 */
(function (doc, win) {
  function adapt (designWidth, rem2px) {
    var d = window.document.createElement('div')
    d.style.width = '1rem'
    d.style.display = 'none'
    var head = window.document.getElementsByTagName('head')[0]
    head.appendChild(d)
    var defaultFontSize = parseFloat(window.getComputedStyle(d, null).getPropertyValue('width'))
    d.remove()
    var w = window.innerWidth > 768 ? 768 : window.innerWidth
    document.documentElement.style.fontSize = w / designWidth * rem2px / defaultFontSize * 100 + '%'
    var st = document.createElement('style')
    var portrait = '@media screen and (min-width:' + w + 'px) {html{font-size:' + ((w / (designWidth / rem2px) / defaultFontSize) * 100) + '%;}}'
    var landscape = '@media screen and (min-width:' + window.innerHeight + 'px) {html{font-size:' + ((window.innerHeight / (designWidth / rem2px) / defaultFontSize) * 100) + '%;}}'
    st.innerHTML = portrait + landscape
    head.appendChild(st)
    return defaultFontSize
  }
  // var resizeEvent = 'orientationchange' in window ? 'orientationchange' : 'resize'
  // if (!doc.addEventListener) return
  // win.addEventListener(resizeEvent, adapt(750, 100), false)
  adapt(750, 100)
})(document, window)



