import Vue from 'vue'
import Router from 'vue-router'

import index from '@/pages/index'
import orderList from '@/pages/orderList'
import member from '@/pages/member'
import list from '@/pages/list'
import projectDetail from '@/pages/projectDetail'
import companyProfile from '@/pages/companyProfile'
import activityList from '@/pages/activityList'
import login from '@/pages/login'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    // {
    //  path: '/',
    //  meta: {
    //    title: '首页'
    //  },
    //  component: index
    // },
    {
      path: '/index',
      name: 'index',
      meta: {
        title: '首页'
      },
      component: index
    },
    {
      path: '/orderList',
      name: 'orderList',
      meta: {
        title: '我的订单'
      },
      component: orderList
    },
    {
      path: '/member',
      name: 'member',
      meta: {
        title: '会员中心'
      },
      component: member
    },
    {
      path: '/list',
      name: 'list',
      meta: {
        title: '项目'
      },
      component: list
    },
    {
      path: '/projectDetail',
      name: 'projectDetail',
      meta: {
        title: '项目详情'
      },
      component: projectDetail
    },
    {
      path: '/companyProfile',
      name: 'companyProfile',
      meta: {
        title: '公司简介'
      },
      component: companyProfile
    },
    {
      path: '/activityList',
      name: 'activityList',
      meta: {
        title: '活动'
      },
      component: activityList
    },
    {
      path: '/login',
      name: 'login',
      meta: {
        title: '登录'
      },
      component: login
    },
    {
      path: '*',
      // component: noFound
      // 重定向
      // redirect: '/home'
      // redirect: {path: '/home'}
      // redirect: {name: 'About'}
      redirect: (to) => {  // 动态设置重定向的目标
        // 目标路由对象，就是访问的路径的路由信息

        if (to.path === '/') {
          return '/index'
        } else {
          return {path: '/index'}
        }

        // console.log(to)

        // return '/home'
      }
    }
  ]
})



